// pages / login

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import {navigateTo} from "gatsby-link";

// Layouts
import Navigation from '../layouts/Navigation';

// Action
import {loginUser} from "../action/UserAction"

// Styles
import styles from '../style/landing.module.css';


class LoginForm extends React.Component {
    componentDidMount() {
        // add root container for styling
        $("#___gatsby").addClass(styles.gatsbyContainer);
    }

    handleSubmit(e) {
        e.preventDefault();
        this.refs.loginBtn.value = "Loading ...";
        document.getElementById("loginBtn").disabled = true;
        this.props.loginUser(this.refs.username.value, this.refs.password.value).then((result) => {
            if (result) {
                localStorage.setItem("user", result.token);

                if(this.props.user.account_type !== 'guard') {
                    navigateTo('/dashboard');
                } else {
                    navigateTo('/dashboard/conversation');
                }
            } else {
                this.refs.loginBtn.value = "Login";
                document.getElementById("loginBtn").disabled = false;
            }
        });
    }

    render() {
        return (
            <div className="wrapperInner">
                <Helmet title="Login Form" meta={[
                    {
                        name: 'description',
                        content: 'Residency.my - Login Form'
                    }, {
                        name: 'keywords',
                        content: 'Residency.my, Login Form'
                    }
                ]}/>

              <Navigation atLandingPage={false}/>

                <div className={styles.loginPageBlock}>
                    <h1 className={styles.loginPageTitle}>Login into Dashboard</h1>
                    <div className={styles.loginPageWarning}>
                        {this.props.error}
                    </div>

                    <form className={styles.formTodo} onSubmit={this.handleSubmit.bind(this)}>
                        <input className={styles.formInput} placeholder="Username" type="text" ref="username" maxLength="100" required />
                        <input className={styles.formInput} placeholder="Password" type="password" ref="password" maxLength="60" required />
                        <input className={styles.formBtn} id="loginBtn" type="submit" ref="loginBtn" value="Login" />
                    </form>

                </div>
            </div>
        );
    }
};


const mapStateToProps = (state) => {
    return {error: state.users.error, user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {loginUser};
}

export default connect(mapStateToProps, mapDispatchToProps())(LoginForm);



