// layouts / Navigation


// Modules
import React from 'react';
import {connect} from 'react-redux';
import Link, { navigateTo } from "gatsby-link";

// Styles
import styles from '../style/landing.module.css';


export default class Navigation extends React.Component {
    render() {

        return (
            <nav className={styles.navigation}>
                <Link className={styles.navTitle} to="/">Residency.my</Link>
                {
                    this.props.atLandingPage ?
                    (
                        <div className={styles.navLinkRightGroup}>
                            <a className={styles.linkRight1 + ' ' + 'page-scroll'} href="#features">Features</a>
                            <a className={styles.linkRight1 + ' ' + 'page-scroll'} href="#how-it-works">How it works</a>
                            <Link className={styles.linkRight1} to="#">About Us</Link>
                            <Link className={styles.login} to="/login">Login</Link>
                        </div>
                    ) :
                    (
                        <div className={styles.navLinkRightGroup}>
                            <Link className={styles.linkRight1 + ' ' + 'page-scroll'} to="/#features">Features</Link>
                            <Link className={styles.linkRight1 + ' ' + 'page-scroll'} to="/#how-it-works">How it works</Link>
                            <Link className={styles.linkRight1} to="#">About Us</Link>
                            <Link className={styles.login} to="/login">Login</Link>
                        </div>
                    )
                }
            </nav>
        );
    }
};



