// Actions / UserAction

// Modules
import axios from "axios";

// Config
import config from "../config";


export function loginUser(username, password) {
    return dispatch => {
        return axios.post(config.serverURL + "/account/login", {
            username,
            password
        }, {
            withCredentials: true
        }).then((res) => {
            dispatch({
                type: "USER_SET",
                payload: res.data
            });
            dispatch({
                type: "USER_ERROR_LOGIN",
                payload: null
            });
            return res.data;
        }).catch((err) => {
            // console.log("I'm error login 1");
            // console.log(err.response.data.error.message);

            dispatch({
                type: "USER_ERROR_LOGIN",
                payload: err.response.data.error.message
            });

            console.log(err);
        })

    }
}


export function setUser(token) {
    return dispatch => {
        dispatch({
            type: "USER_SET",
            payload: token
        });
    }
}

export function logOutUser() {
    return dispatch => {
        return axios.get(config.serverURL + "/account/logout", {
            withCredentials: true
        }).then((res) => {
            dispatch({type: "USER_LOGOUT"});
            dispatch({type: "MESSAGE_CLEAR"});
            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}


export function setUserExperience(arg) {
    return dispatch => {
        dispatch({
            type: "USER_EXP_SET",
            payload: arg
        });
    }
}



